.blog-container {
  min-height: 100vh;
  
  .blog-title {
    font-size: 2.5rem;
    font-weight: bold;
    margin-bottom: 2rem;
    text-align: center;
    color: white;
  }
  
  .blog-posts-list {
    max-width: 800px;
    margin: 0 auto;
  }
  
  .blog-post-card {
    transition: all 0.3s ease;
    
    .blog-post-title {
      color: white;
      font-weight: bold;
      margin-bottom: 0.5rem;
    }
    
    .blog-post-date {
      color: rgba(255, 255, 255, 0.7);
      font-size: 0.9rem;
      margin-bottom: 1rem;
    }
    
    .blog-post-excerpt {
      color: rgba(255, 255, 255, 0.9);
      line-height: 1.6;
    }
  }

  // Individual blog post styles
  .blog-post-container {
    padding: 2rem 0;
    max-width: 800px;
    margin: 0 auto;
    
    .back-button {
      color: white;
      border: 1px solid rgba(255, 255, 255, 0.3);
      
      &:hover {
        background-color: rgba(255, 255, 255, 0.1);
        border-color: rgba(255, 255, 255, 0.5);
      }
    }
    
    .blog-post-content {
      .blog-post-header {
        margin-bottom: 2rem;
        
        .blog-post-title {
          color: white;
          font-weight: bold;
          margin-bottom: 1rem;
          line-height: 1.2;
        }
        
        .blog-post-meta {
          display: flex;
          gap: 1rem;
          
          .blog-post-date,
          .blog-post-read-time {
            color: rgba(255, 255, 255, 0.7);
            font-size: 0.9rem;
          }
        }
      }
      
      .blog-post-body {
        color: rgba(255, 255, 255, 0.9);
        line-height: 1.8;
        font-size: 1.1rem;
        
        h1, h2, h3 {
          color: white;
          margin: 2rem 0 1rem 0;
          font-weight: bold;
        }
        
        h1 {
          font-size: 2rem;
        }
        
        h2 {
          font-size: 1.5rem;
        }
        
        h3 {
          font-size: 1.25rem;
        }
        
        p {
          margin-bottom: 1.5rem;
        }
        
        ul, ol {
          margin: 1rem 0;
          padding-left: 2rem;
          
          li {
            margin-bottom: 0.5rem;
            color: rgba(255, 255, 255, 0.9);
          }
        }
        
        strong {
          color: white;
          font-weight: bold;
        }
        
        code {
          background-color: rgba(255, 255, 255, 0.1);
          padding: 0.2rem 0.4rem;
          border-radius: 4px;
          font-family: 'Courier New', monospace;
          color: #f8f8f2;
        }
        
        pre {
          background-color: rgba(255, 255, 255, 0.1);
          padding: 1rem;
          border-radius: 8px;
          overflow-x: auto;
          margin: 1.5rem 0;
          
          code {
            background: none;
            padding: 0;
          }
        }
      }
    }
  }
}

// Light mode styles
.light-mode {
  .blog-container {
    .blog-title {
      color: #333;
    }
    
    .blog-post-card {
      background-color: rgba(0, 0, 0, 0.05) !important;
      border: 1px solid rgba(0, 0, 0, 0.1) !important;
      
      &:hover {
        background-color: rgba(0, 0, 0, 0.1) !important;
      }
      
      .blog-post-title {
        color: #333;
      }
      
      .blog-post-date {
        color: rgba(0, 0, 0, 0.6);
      }
      
      .blog-post-excerpt {
        color: rgba(0, 0, 0, 0.8);
      }
    }

    // Light mode individual blog post styles
    .blog-post-container {
      .back-button {
        color: #333;
        border-color: rgba(0, 0, 0, 0.3);
        
        &:hover {
          background-color: rgba(0, 0, 0, 0.1);
          border-color: rgba(0, 0, 0, 0.5);
        }
      }
      
      .blog-post-content {
        .blog-post-header {
          .blog-post-title {
            color: #333;
          }
          
          .blog-post-meta {
            .blog-post-date,
            .blog-post-read-time {
              color: rgba(0, 0, 0, 0.6);
            }
          }
        }
        
        .blog-post-body {
          color: rgba(0, 0, 0, 0.8);
          
          h1, h2, h3 {
            color: #333;
          }
          
          ul, ol {
            li {
              color: rgba(0, 0, 0, 0.8);
            }
          }
          
          strong {
            color: #333;
          }
          
          code {
            background-color: rgba(0, 0, 0, 0.1);
            color: #333;
          }
          
          pre {
            background-color: rgba(0, 0, 0, 0.05);
          }
        }
      }
    }
  }
}

.blog-image-container {
  margin: 2rem 0;
  text-align: center;
  
  .blog-image {
    border-radius: 8px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease;
    
    &:hover {
      transform: scale(1.02);
    }
  }
  
  .blog-image-caption {
    margin-top: 0.5rem;
    font-style: italic;
    color: #666;
    font-size: 0.9rem;
  }
} 